<script>
import { Modal } from 'bootstrap';
// eslint-disable-next-line import/no-extraneous-dependencies
import { jsPDF } from 'jspdf';
// eslint-disable-next-line import/no-extraneous-dependencies
import * as html2canvas from 'html2canvas';

export default {
  data() {
    return {
      pdfModal: '',
      tempData: {},
      tempCompany: {},
    };
  },
  methods: {
    download() {
      const A4Width = 595.28;
      const A4Height = 841.89;

      html2canvas(this.$refs.pdf, {
        scale: 2,
        useCORS: true,
        backgroundColor: '#fff',
      }).then((canvas) => {
        const pageHeight = (canvas.width / A4Width) * A4Height;
        let leftHeight = canvas.height;
        let position = 0;
        const imgWidth = A4Width;
        const imgHeight = (A4Width / canvas.width) * canvas.height;

        const pageData = canvas.toDataURL('image/jpeg', 1.0);
        // eslint-disable-next-line new-cap
        const PDF = new jsPDF('p', 'pt', 'a4');
        if (leftHeight < pageHeight) {
          PDF.addImage(pageData, 'JPEG', 0, 0, imgWidth, imgHeight);
        } else {
          while (leftHeight > 0) {
            PDF.addImage(pageData, 'JPEG', 0, position, imgWidth, imgHeight);
            leftHeight -= pageHeight;
            position -= A4Height;
            if (leftHeight > 0) PDF.addPage();
          }
        }

        PDF.save('test.pdf');
      });
    },
    showModal() {
      this.pdfModal.show();
    },
  },
  mounted() {
    this.pdfModal = new Modal(this.$refs.pdfModal);
    this.$emitter.on('pdfEmit', (e) => {
      this.tempData = e;
      this.tempCompany = this.tempData.company;
      this.showModal();
    });
  },
};
</script>
