    data-bs-backdrop="static"
    data-bs-keyboard="false"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    ref="AddEdit"
  >
    <div class="modal-dialog modal-xl">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5" id="exampleModalLabel">
            <span v-if="status">新增工單</span><span v-else>修改工單</span>
          </h1>
          <button
            type="button"
            class="btn-close"
            @click="hideModal"
            aria-label="Close"
          ></button>
        </div>
        <VForm
          ref="formRefSignin"
          id="profileForm"
          v-slot="{ meta }"
          @submit="addEditOrder"
        >
          <div class="modal-body row row-cols-2">
            <section>
              <!-- 廠商 -->
              <div class="form-floating mb-3">
                <VField
                  name="廠商"
                  type="text"
                  :rules="isCompanyExist"
                  class="form-control"
                  list="datalistOptions"
                />
                <datalist id="datalistOptions">
                  <template v-for="(item, index) in companyList" :key="index">
                    <option :value="item.companyName"></option>
                  </template>
                </datalist>
                <label for="floatingInput"
                  >廠商名稱<ErrorMessage name="廠商" class="text-danger ms-3"
                /></label>
              </div>
              <!-- 施工地址 -->
              <div class="form-floating mb-3">
                <VField
                  name="施工地址"
                  rules="required"
                  type="text"
                  v-model="tempData.address"
                  class="form-control"
                />
                <label for="floatingInput"
                  >施工地址<ErrorMessage
                    name="施工地址"
                    class="text-danger ms-3"
                /></label>
              </div>
              <!-- 施工日期 -->
              <div class="form-floating mb-3">
                <VField
                  name="施工日期"
                  rules="required"
                  type="date"
                  v-model="tempData.constructionDate"
                  class="form-control"
                />
                <label for="floatingInput"
                  >施工日期<ErrorMessage
                    name="施工日期"
                    class="text-danger ms-3"
                /></label>
              </div>
              <!-- 付款方式 -->
              <div class="form-floating mb-3">
                <VField
                  name="付款方式"
                  as="select"
                  rules="required"
                  class="form-control"
                  v-model="tempData.paymentMethod"
                >
                  <option value="現金">現金</option>
                  <option value="支票">支票</option>
                </VField>
                <label for="floatingInput"
                  >付款方式<ErrorMessage
                    name="付款方式"
                    class="text-danger ms-3"
                /></label>
              </div>
              <!-- 總金額 -->
              <div class="form-floating mb-3">
                <VField
                  name="總金額"
                  type="number"
                  rules="required"
                  v-model="tempData.total"
                  class="form-control"
                />
                <label for="floatingInput"
                  >總金額<ErrorMessage name="總金額" class="text-danger ms-3"
                /></label>
              </div>
              <!-- 保留款 -->
              <div class="form-floating mb-3">
                <VField
                  name="保留款"
                  type="number"
                  v-model="tempData.retainage"
                  class="form-control"
                />
                <label for="floatingInput"
                  >保留款<ErrorMessage name="保留款" class="text-danger ms-3"
                /></label>
              </div>
            </section>
            <section>
              <!-- 工人 -->
              <h5>工人：</h5>
              <!-- 新增 -->
              <div v-if="status">
                <div class="form-floating mb-3">
                  <div class="row row-cols-3 row-cols-md-4 g-3 mb-3">
                    <div
                      class="form-check"
                      v-for="(item, index) in workerList"
                      :key="index"
                    >
                      <input
                        class="form-check-input"
                        type="checkbox"
                        :value="item"
                        :id="`worker${index}`"
                        v-model="tempData.workers"
                      />
                      <label class="form-check-label" :for="`worker${index}`">{{
                        item.name
                      }}</label>
                    </div>
                  </div>
                </div>
                <template
                  v-for="(item, index) in tempData.workers"
                  :key="index"
                >
                  <div class="input-group mb-2">
                    <input
                      type="text"
                      placeholder="工人姓名"
                      class="form-control"
                      :value="item.name"
                      disabled
                    />
                    <input
                      type="text"
                      placeholder="工人電話"
                      class="form-control"
                      :value="item.phone"
                      disabled
                    />
                    <input
                      type="number"
                      placeholder="工作時數"
                      class="form-control"
                      v-model="tempData.workers[index].time"
                    />
                  </div>
                </template>
              </div>
              <!-- 修改 -->
              <div v-else>
                <!-- <div class="form-floating mb-3">
                  <div class="row row-cols-3 row-cols-md-4 g-3 mb-3">
                    <div
                      class="form-check"
                      v-for="(item, index) in workerList"
                      :key="index"
                    >
                      <input
                        class="form-check-input"
                        type="checkbox"
                        :value="item"
                        :id="`worker${index}`"
                        v-model="tempData.workers"
                      />
                      <label class="form-check-label" :for="`worker${index}`">{{
                        item.name
                      }}</label>
                    </div>
                  </div>
                </div> -->
                <template
                  v-for="(item, index) in tempData.workers"
                  :key="index"
                >
                  <div class="input-group mb-2">
                    <input
                      type="text"
                      placeholder="工人姓名"
                      class="form-control"
                      v-model="item._id.name"
                      disabled
                    />
                    <input
                      type="text"
                      placeholder="工人電話"
                      class="form-control"
                      v-model="item._id.phone"
                      disabled
                    />
                    <input
                      type="number"
                      placeholder="工作時數"
                      class="form-control"
                      v-model="tempData.workers[index].time"
                    />
                  </div>
                </template>
              </div>
              <hr />
              <!-- 調用工人 -->
              <div>
                <h5>調用工人：</h5>
                <template
                  v-for="(item, index) in tempData.transferWorkers"
                  :key="index"
                >
                  <div class="input-group mb-2">
                    <input
                      type="text"
                      placeholder="工人姓名"
                      class="form-control"
                      v-model="tempData.transferWorkers[index].name"
                    />
                    <input
                      type="text"
                      aria-label="Last name"
                      placeholder="工人電話"
                      class="form-control"
                      v-model="tempData.transferWorkers[index].phone"
                    />
                    <input
                      type="number"
                      aria-label="Last name"
                      placeholder="工作時數"
                      class="form-control"
                      v-model="tempData.transferWorkers[index].time"
                    />
                    <button
                      class="btn btn-outline-danger"
                      type="button"
                      id="button-add"
                      @click="deleteTransArray(index)"
                    >
                      刪除
                    </button>
                  </div>
                </template>
                <div class="d-flex justify-content-center">
                  <button
                    class="btn btn-outline-dark"
                    type="button"
                    @click="addTransArray"
                  >
                    新增
                  </button>
                </div>
              </div>
            </section>
          </div>
          <div class="modal-footer justify-content-between">
            <button
              type="button"
              class="btn btn-outline-danger"
              v-if="!status"
              @click="deleteOrder(tempData._id)"
            >
              刪除該工單
            </button>
            <div></div>
            <button
              type="submit"
              class="btn btn-primary"
              :disabled="!meta.valid"
            >
              儲存
            </button>
          </div>
        </VForm>
      </div>
    </div>
  </div>
</template>

<script>
import { Modal } from 'bootstrap';
import {
  apiGetCompanys,
  apiGetWorkers,
  apiPostOrder,
} from '@/assets/javascript/api';

export default {
  data() {
    return {
      modal: '',
      status: false,
      tempData: {
        address: '',
        company: '',
        status: 0,
        paymentMethod: '',
        total: 0,
        retainage: 0,
        constructionDate: '',
        workers: [],
        transferWorkers: [],
      },
      tempWorker: [],
      companyList: [],
      workerList: [],
    };
  },
  watch: {},
  methods: {
    isCompanyExist(value) {
      this.tempData.company = '';
      let companyId = '';
      // eslint-disable-next-line consistent-return
      this.companyList.forEach((item) => {
        if (item.companyName === value) {
          companyId = item.company;
          // eslint-disable-next-line no-underscore-dangle
          this.tempData.company = item._id;
        }
      });
      return companyId !== '' ? true : '查無此廠商';
    },
    getCompany() {
      apiGetCompanys()
        .then((res) => {
          this.companyList = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getWorker() {
      apiGetWorkers()
        .then((res) => {
          this.workerList = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    addEditOrder() {
      // eslint-disable-next-line no-underscore-dangle
      if (this.tempData._id) {
        console.log('這是修改');
      } else {
        apiPostOrder(this.tempData)
          .then((res) => {
            console.log(res);
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    addTransArray() {
      this.tempData.transferWorkers.push({});
    },
    deleteTransArray(index) {
      this.tempData.transferWorkers.splice(index, 1);
    },
    showModal() {
      this.modal.show();
    },
    hideModal() {
      this.tempData = {
        address: '',
        company: '',
        status: 0,
        paymentMethod: '',
        total: 0,
        retainage: 0,
        constructionDate: '',
        workers: [],
        transferWorkers: [],
      };
      this.modal.hide();
    },
  },
  mounted() {
    this.getCompany();
    this.getWorker();
    this.modal = new Modal(this.$refs.AddEdit);
    this.$emitter.on('addEditOrder', (e) => {
      this.status = e.status;
      if (!e.status) {
        this.tempData = JSON.parse(JSON.stringify(e.data));
      }
      this.showModal();
    });
  },
};
</script>
